import Vue from 'vue'
import Router from 'vue-router'

import Index from 'views/welcome/index.vue'
import Login from 'views/login/index.vue'

Vue.use(Router)

const routes = [
	{
		path: '/login',
		name: '登录',
		component: Login
	},
	{
		path: '/',
		name: '首页',
		meta: {
			title: '页面列表'
		},
		component: Index,
	},
	{
		path: '/add',
		name: 'add-page',
		meta: {
			title: '新增页面'
		},
		component: () => import(/* webpackChunkName: "edit" */ 'views/base/index.vue')
	}, {
		path: '/edit/:id',
		name: 'edit-page',
		meta: {
			title: '编辑页面'
		},
		component: () => import(/* webpackChunkName: "edit" */ 'views/base/index.vue')
	}, {
		path: '/copy/:id',
		name: 'copy-page',
		meta: {
			title: '复制页面'
		},
		component: () => import(/* webpackChunkName: "edit" */ 'views/base/index.vue')
	},
]

const router = new Router({
	routes: routes.concat([])
})

//重定向登录
router.beforeEach((to, from, next) => {
	try {
		document.title = 'Force' + ( to.meta.title ? `::${to.meta.title}` : '')
	} catch (e) {}
	next()
})

export default router





