<template>
	<div class="previewer-container">
		<iframe class="preview-wrapper" :src="src.replace(/https:\/\//ig, 'http://')" frameborder="0"></iframe>
	</div>
</template>
<script>
	export default {
		name: 'previewer',
		props: {
			src: {
				type: String,
				default: '',
				required: true
			}
		},
		data () {
			return {}
		}
	}
</script>
<style lang="less" scoped>
	.previewer-container {
		width: @device-width;
		height: @device-height;
		border: 1px solid #e9e9e9;
	}

	.preview-wrapper {
		position: relative;
		width: @device-width;
		height: @device-height;

	}

	.preview-title {
		position: relative;
		height: 56px;
		border-bottom: 1px solid #e6e6e6;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		.title-back {
			width: 20%;
			display: flex;
			align-items: center;
			.button-back {
				margin-left: 10px;
				font-size: 16px;
				color: rgba(0, 0, 0, .5);
			}
		}
		.title-text {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 50%;
			font-size: 16px;
			transform: translate(-50%, -50%);
			text-align: center;
		}
		.title-share {
			width: 12%;
			display: flex;
			justify-content: center;
			.button-share {
				text-align: center;
				width: 20px;
				font-size: 20px;
				color: rgba(0, 0, 0, .5);

			}
		}
	}
</style>



