<template>
  <div class="login">
    <el-card class="login-card">
      <el-form :model="model" class="login-form">
        <el-form-item label="用户名">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item class="button-group">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button>注册</el-button>
          <el-button type="text" @click="findPassword">忘记密码?</el-button>
        </el-form-item>
      </el-form>
    </el-card>

  </div>
</template>
<script>

  export default {
    name: 'login',
    data() {
      return {
        model: {
          username: '',
          password: ''
        }
      }
    },
    methods: {
      findPassword() {
        this.$message({
          showClose: false,
          message: '你咋不把自己忘了呢?',
          type: 'error'
        })
      },
      login(){

      }
    }
  }
</script>
<style lang="less">
  .login {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f57;
    .login-card {
      position: fixed;
      left: 50%;
      top: 50%;
      padding: 0 35px;

      width: 300px;
      transform: translateX(-50%) translateY(-50%);
      border-radius: 10px;
      overflow: hidden;
      .login-form {
        width: 100%;
        background: #fff;
        .forget-password {
          margin-right: 0;
        }
      }
    }

  }
</style>



