<template>
	<div class="app-route-picker">
		<el-input class="app-route-input" v-model="currentRoute" placeholder="请选择路由" readonly>
			<el-button slot="prepend" :icon="currentRoute?'el-icon-edit':'el-icon-plus'" @click="pickAppRoute"></el-button>
			<el-button v-if="currentRoute" slot="append" icon="el-icon-delete" @click="clear"></el-button>
		</el-input>
	</div>
</template>
<script>
	export default {
		name: 'app-route-picker',
		props: {
			value: {
				type: String,
				default: ''
			}
		},
		data () {
			return {
				select: '',
				currentRoute: this.value
			}
		},
		watch: {
			currentRoute (val) {
				this.$emit('input', val)
			}
		},
		methods: {
			pickAppRoute () {
				this.$appRoutePicker.pick(this.currentRoute, (newRoute) => {
					this.currentRoute = newRoute
				})
			},
			clear () {
				this.$appRoutePicker.installed ? this.$appRoutePicker.clear() : this.currentRoute = ''
			}
		}
	}
</script>
<style lang="less" scoped>
	.app-route-picker {

	}

	.app-route-input {
		&:hover {
			cursor: pointer;
		}
	}
</style>



