import * as types from '../mutations-types'

const pickers = {
	state: {
		pickingImage: false
	},
	mutations: {
		[types.HANDLE_IMAGE_PICKER] (state, picking) {
			state.pickingImage = picking
		}
	},
	getters: {

	}
}

export default pickers



