import Vue from 'vue'
import Previewer from './previewer.vue'
import UrlForm from './url-form.vue'

const PagePreviewerConstructor = Vue.extend()

let instance
let inserted = false
let installed = false

const initPagePreviewer = (url, title) => {
	instance = new PagePreviewerConstructor({
		el: document.createElement('div'),
		data: () => ({
			title,
			src: url,
			visible: true
		}),
		components: {
			Previewer,
			UrlForm
		},
		render (h) {
			// --- 模态框 start ---
			return h('el-dialog', {
				props: {
					visible: this.visible,
					'lock-scroll': true,
					'append-to-body': true,
					width: '800px',
					'custom-class': 'page-previewer-common'

				},
				'class': [],
				on: {
					close: () => {
						this.visible = false
					}
				}
				// --- 模态框 end ---
			}, [
				h('div', {
					style: {
						display: 'flex',
						'justify-content': 'space-around',
					}
				}, this.visible ? [
					h('previewer', {
						props: {
							src: this.src,
						}
					}),
					// 右边预览数据区
					h('url-form', {
						props: {
							url: this.src,
							title: this.title
						}
					})
				] : [])

			])
		}
	})
	installed = true
}

const previewPage = (url, title) => new Promise((resovle) => {
	if (!instance) {
		initPagePreviewer(url, title)
	}

	instance.src = url
	instance.title = title

	if (!inserted) {
		document.body.appendChild(instance.$el)
		inserted = true
	}
	if (!instance.visible) {
		Vue.nextTick(() => {
			instance.visible = true
		})
	}
})

const PagePreviewer = () => {
	if (Vue.prototype.$isServer) return
	return {
		preview: previewPage
	}
}

export default PagePreviewer

export { PagePreviewer }



