import * as types from '../mutations-types'

const config = {
	state: {
		type: '',
		handle: '',

		componentKey: 0,
		validation: true,
		component: {},
		configurations: {
			items: [],
			components: []
		},
	},
	mutations: {
		[types.CHANGE_CONFIG] (state, newConfigurations) {
			state.configurations = {...newConfigurations}
			// console.log(`配置已于 ${new Date().toLocaleString()} 更新！ from CHANGE_CONFIG`)
		},
		[types.PUSH_LIST] (state, component) {
			state.component = {...component}
			state.configurations = {...state.configurations}
			// console.log(`配置已于 ${new Date().toLocaleString()} 更新！from PUSH_LIST`)
		},
		[types.MARKKEY] (state, key) {
			if (!state) return
			state.componentKey += key
		},
		[types.RESETKEY] (state) {
			state.componentKey = 0
		},
		[types.VALIDATE] (state, validation) {
			state.validation = !!validation
		}
	},
	getters: {
		pageTitle (state) {
			let title = '美甲帮'
			if (!state.configurations.items) return ''
			state.configurations.items.map(item => {
				if (item.realProp === 'title') {
					title = item.value
				}
			})
			return title
		},
		showShare (state) {
			let show = false
			if (!state.configurations.items) return show
			state.configurations.items.map(item => {
				if (item.realProp === 'title-config') {
					show = item.value.indexOf('分享') > -1
				}
			})
			return show
		}
	}
}

export default config



