import { HANDLE_CONFIG_COLLAPSE, HANDLE_DRAGGING, HANDLE_PAGE_CONFIG, HANDLE_SIDE_COLLAPSE } from '../mutations-types'

const layout = {
	state: {
		sideCollapse: false,
		configCollapse: false,
		dragging: false,
		showPageConfig: true

	},
	mutations: {
		[HANDLE_SIDE_COLLAPSE]: (state) => {
			state.sideCollapse = !state.sideCollapse
		},
		[HANDLE_CONFIG_COLLAPSE]: (state) => {
			state.configCollapse = !state.configCollapse
		},
		[HANDLE_DRAGGING]: (state) => {
			state.dragging = !state.dragging
		},
		[HANDLE_PAGE_CONFIG]: (state) => {
			state.showPageConfig = !state.showPageConfig
		}
	}
}

export default layout



