<template>
	<el-card class="welcome-container" v-loading="loading">
		<!--<h1>原力与你同在！</h1>-->
		<!--按钮组-->
		<!--<div slot="header" class="btns">-->
		<!--<el-button type="primary" @click="addNewPage">新增页面-->
		<!--</el-button>-->
		<!--</div>-->
		<!--搜索-->
		<el-form inline>
			<el-form-item label="ID">
				<el-input @keyup.enter.native="search" v-model="searchOptions.id" placeholder="请输入页面id" clearable></el-input>
			</el-form-item>
			<el-form-item label="标题">
				<el-input @keyup.enter.native="search" size="small" v-model="searchOptions.title" placeholder="请输入页面标题" clearable></el-input>
			</el-form-item>
			<el-form-item label="状态">
				<el-select @change="search" v-model="searchOptions.status" placeholder="请选择上线状态" clearable>
					<el-option v-for="(item, index) in statusOptions" :key="index" :label="item.label" :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item>
				<template>
					<el-button @click="search">搜索</el-button>
				</template>
			</el-form-item>
			<el-form-item class="float-right">
				<template>
					<el-button type="primary" @click="addNewPage">新增页面
					</el-button>
				</template>
			</el-form-item>
		</el-form>

		<!--列表-->
		<div class="list-view">
			<el-table :data="pages" border style="width: 100%" key="force-index" :height="innerHeight">
				<el-table-column prop="id" label="页面ID" width="100"></el-table-column>
				<el-table-column prop="title" label="标题"></el-table-column>
				<el-table-column prop="status" label="状态" width="120" align="center">
					<template slot-scope="scope">
						{{getStatusLabel(scope.row.status, scope.row.admin_name)}}
					</template>
				</el-table-column>
				<el-table-column prop="show_start_time" label="上线时间" align="center">
					<template slot-scope="scope">
						{{parsedTime([scope.row.show_start_time, scope.row.show_end_time])}}
					</template>
				</el-table-column>
				<el-table-column label="操作" fixed="right" width="350" align="center">

					<el-button-group slot-scope="scope">
						<el-button @click="handleEdit(scope.row)">编辑
						</el-button>
						<el-button :disabled="!scope.row.web_url" @click="handlePreview(scope.row.web_url,scope.row.title)">预览
						</el-button>
						<el-button @click="handleCopy(scope.row.id)">复制
						</el-button>
						<el-button @click="handleAnalyse(scope.row.web_url)">统计
						</el-button>
						<el-button @click="handleDelete(scope.row.id)">删除
						</el-button>
					</el-button-group>

				</el-table-column>
			</el-table>
		</div>

		<!--分页控制器-->
		<div class="pagination-view">
			<el-pagination background layout="prev, pager, next" :current-page.sync="currentPage" :page-size="limit" :total="pagesCount" @current-change="changePagination"></el-pagination>
		</div>

	</el-card>
</template>
<script>
	import { format } from 'date-fns'

	export default {
		name: 'welcome',
		data () {
			return {
				innerHeight: Math.max([innerHeight - 200, 600]),
				loading: true,
				limit: 12,
				currentPage: 1,
				pages: [],
				pagesCount: 0,
				blank: window.parent !== window,
				searchOptions: {
					id: '',
					title: '',
					status: ''
				},
				statusOptions: [
					{
						value: 2,
						label: '已下线'
					}, {
						value: 1,
						label: '已上线'
					}, {
						value: 0,
						label: '未上线'
					}],
				tableData: [{
					data_id: '123456',
					data_title: '一个H5',
					data_type: '未上线',
					data_time: '2018-01-23'
				}]
			}
		},
		methods: {

			search () {
				this.currentPage = 1
				this.getPages(this.searchOptions)
			},
			changePagination () {
				this.getPages(this.searchOptions)
			},
			async getPages (options = {}) {
				this.loading = true
				await this.$api.getPageList({
					id: options.id || '',
					title: options.title || '',
					status: options.status === 0 ? 0 : (options.status || ''),
					offset: this.limit * (this.currentPage - 1),
					limit: this.limit

				}).then(res => {
					this.pages = res.list || []
					this.pagesCount = res.total
				}).catch(e => {
					console.log(e)
					this.pages = []
				})
				this.loading = false
			},
			getStatusLabel (status = 0, admin) {
				if (admin) return `${admin}正在编辑页面`
				return this.statusOptions.find(option => option.value === status).label
			},
			parsedTime (timestamp) {
				if (Array.isArray(timestamp)) {
					if (timestamp[0] === 0 && timestamp[1] === 0) {
						return '未设置'
					}
					return timestamp.map(ts => this.parsedTime(ts)).join(' - ')
				} else if (timestamp === 0) {
					return '未设置'
				}
				return format((Number(timestamp) * 1000), 'YYYY-MM-DD HH:mm:ss')
			},
			addNewPage () {
//				this.$router.push({name:'add-page'})
				this.blank ? window.open('#/add') : this.$router.push({name: 'add-page'})
			},
			handleEdit (row) {

				this.blank ? window.open('#/edit/' + row.id) : this.$router.push({
					name: 'edit-page',
					params: {
						id: row.id
					}
				})
			},
			handlePreview (url, title) {
				if (!url || !title) {
					this.$message.warning('无法预览')
				}
				this.$pagePreviewer.preview(url, title)
				// window.open(url, '', 'width=375,height=603')
			},
			handleCopy (id) {
				this.blank ? window.open('#/copy/' + id)
					: this.$router.push({
						name: 'copy-page',
						params: {
							id
						}
					})
			},
			handleAnalyse (url) {
				if (!url) {
					return this.$message.warning('无统计数据')
				}
				console.log(url)
				window.open(`/admin.php?m=admin&c=WebTracking&a=index&url=${encodeURIComponent(url)}&app_name=force`)
			},
			handleDelete (id) {
				this.$confirm('此操作将永久删除该活动, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$api.deletePage(id).then(response => {
						if (response.error_code === 0) {
							this.$message.success('删除成功!')
							this.getPages()
						} else {
							this.$message.warning(response.error_message || response.error_description)
						}
					}).catch(err => {
						this.$message.error(err.message)
					})
				}).catch((e) => {

				})
			}
		},
		created () {
			this.getPages()
			console.log(window.parent === window)
		}
	}
</script>
<style lang="less" scoped>
	.welcome-container {
		padding-top: 20px;
		min-width: 1024px;
	}

	.btns {
		margin: 10px 0;
		display: flex;
		justify-content: flex-end;
	}

	.filtrate-view {
		display: flex;
		align-items: center;
		align-content: center;
		justify-content: center;
		li {
			color: teal;
			display: flex;
			align-items: center;
			margin-right: 10px;
			label {
				white-space: nowrap;
				margin-right: 5px;
			}
			button {
				background-color: teal;
			}
		}
	}

	.list-view {
	}

	.pagination-view {
		text-align: center;
		margin: 15px auto 10px;
	}

	.float-right {
		float: right;
		&:after{
			clear: both;
		}
	}

</style>



