import postMessage from './post-message'
import axios from 'axios'

export default {
	install (Vue) {
		const Api = {
			get: (path, options) => {
				return axios.get(path, options).then(res => {
					const data = res.data
					if (data.error_code === 20001) {
						return window.location.href = '/boss'
					}
					return data
				})
			},
			post: (path, data, config) => {
				return axios.post(path, data, config).then(res => res.data).catch(e => e)
			},
			getList (options = {}) {
				return axios.get('/boss/force/pages', {
					params: {
						id: options.id || '',
						status: options.status === 0 ? 0 : (options.status || ''),
						title: options.title || '',
						limit: options.limit || 0,
						offset: options.offset || 0,
						fields: '{id,title,show_start_time,show_end_time,status,admin_name,bundle_url,web_url}'
					}
				}).then(res => {
					const data = res.data
					if (data.error_code === 20001) {
						return window.location.href = '/boss'
					}
					return data
				}).catch(e => e)
			},
			getListInfo (options = {}) {
				return axios.get('/boss/force/list-page-info', {
					params: {
						id: options.id || '',
						status: options.status === 0 ? 0 : (options.status || ''),
						title: options.title || '',
					}
				}).then(res => {
					return res.data
				}).catch(e => e)
			},
			getPageList (options = {}) {

				return axios.all([this.getListInfo(options), this.getList(options)]).then(axios.spread((info, list) => {
					if (info.data && list.data) {

						return {total: info.data.total, list: list.data}
					}
					Vue.prototype.$message.warning('无数据')
					return {total: 0, list: []}
				})).catch(err => Vue.prototype.$message.error(err.message))

			},
			getPageInfo (id = 0, is_preview) {
				return axios.get(`/boss/force/pages/${id}.json`, {
					params: {
						is_preview: !!is_preview,
						fields: '{id,title,bg_color,title_config,share_title,share_image,share_link,show_start_time,show_end_time,config_data,share_text,status,share_text,content,admin_name,web_url}'
					}
				}).then(res => {
					return res.data
				}).catch(e => e)
			},
			postPageDraft (options = {}) {
				const id = options.id
				let config_data = options.config_data
				if (typeof(config_data) !== 'string') {
					config_data = JSON.stringify(config_data)
				}
				if (!id || !options.config_data) {
					return new Promise((resolve, reject) => {
						reject('参数不完整')
					})
				}
				return axios.post(`/boss/force/pages/save`, {
					id,
					config_data
				}).then(res => res.data).catch(e => e)
			},
			postPageInfo (options = {}) {
				return axios.post(`/boss/force/pages`, {
					title: options.title || '',
					bg_color: options.bg_color || '',
					title_config: options.title_config || '',
					share_title: options.share_title || '',
					share_image: options.share_image || '',
					share_link: options.share_link || '',
					show_start_time: options.show_start_time || 0,
					show_end_time: options.show_end_time || 0,
					config_data: options.config_data || JSON.stringify({}),
					share_text: options.share_text || '',
					content: options.content || `<template></template>`
				}).then(res => res.data).catch(e => e)

			},
			putPageInfo (options) {
				return axios.put(`/boss/force/pages/${options.id}`, {
					title: options.title || '',
					bg_color: options.bg_color || '',
					title_config: options.title_config || '',
					share_title: options.share_title || '',
					share_image: options.share_image || '',
					share_link: options.share_link || '',
					show_start_time: options.show_start_time || 0,
					show_end_time: options.show_end_time || 0,
					config_data: options.config_data || JSON.stringify({}),
					share_text: options.share_text || '',
					content: options.content || `<template></template>`
				}).then(res => res.data).catch(e => e)
			},
			deletePage (id) {
				return axios.post('/boss/force/pages/' + ( id || 0), {}).then(res => res.data).catch(e => e)
			},
			postMessage
		}
		Vue.prototype.$api = Api
	}
}



