<template>
	<div class="force-image-picker" @click="pick" @mouseenter="showIcon=true" @mouseleave="showIcon=false">
		<template v-if="imageUrl">
			<img :style="{width: width, height: height}" :src="imageUrl" class="preview">
			<div v-if="showIcon" class="picker-wrapper" @click.stop>
				<i class="el-icon-picture image-picker-icon" @click.stop="pick"></i>
				<i class="el-icon-delete image-picker-icon" @click.stop="clear"></i>
				<i class="el-icon-zoom-in image-picker-icon" @click.stop="zoomIn"></i>
				<a :href="imageUrl" :download="imageUrl">
					<i class="el-icon-download image-picker-icon" @click.stop></i>
				</a>
			</div>
		</template>
		<i class="el-icon-picture image-picker-icon" v-else></i>
		<p class="size-box">高度：{{realHeight}}px<br/>宽度：{{realWidth}}px</p>
	</div>
</template>
<script>

	export default {
		name: 'image-picker',
		props: {
			value: {
				type: String,
				default: ''
			},
			objId: {
				type: Number,
				default: 0
			},
			objType: {
				type: String,
				default: 'h5SimplePage'
			},
			mode: {
				type: String,
				default: 'replace'
			},
			format: {
				type: String,
				default: 'url' // path url
			}
		},
		watch: {
			value () {
				this.imageUrl = this.value.split('|').length > 2 ? this.value.split('|')[0] : this.value
			}
		},
		data () {
			return {
				showIcon: false,
				imageUrl: this.value.split('|').length > 2 ? this.value.split('|')[0] : this.value,
				realWidth: this.value.split('|').length > 2 ? this.value.split('|')[1] : 0,
				realHeight: this.value.split('|').length > 2 ? this.value.split('|')[2] : 0,
				height: '100px',
				width: '100px'
			}
		},

		methods: {
			pick () {
				this.setImagePickerParam()
				this.$imagePicker.pick()
			},
			clear () {
				this.imageUrl = ''
				this.$emit('input', '')
			},
			zoomIn () {
				this.$imagePicker.preview(this.imageUrl)
			},
			setPreviewSize () {
				if (Number(this.realWidth) >= Number(this.realHeight)) {
					this.width = '100px'
					this.height = this.realHeight / this.realWidth * 100 + 'px'
				} else {
					this.height = '100px'
					this.width = this.realWidth / this.realHeight * 100 + 'px'
				}
			},
			setImagePickerParam () {
				window.imagePickerParam = {
					hide: () => {
						this.$imagePicker.hide()
					},
					getData: (data, width = 0, height = 0) => {
						width = ~~width
						height = ~~height
						if (width === 0 || height === 0) {
							return
						}
						this.imageUrl = data
						this.realWidth = width
						this.realHeight = height
						this.setPreviewSize()
						this.$emit('input', `${data}|${width}|${height}`)
					}
				}
				window.imagePickerParam.el = 'image-picker'
				window.imagePickerParam.mode = this.mode
				window.imagePickerParam.format = this.format
				window.imagePickerParam.objId = this.objId
				window.imagePickerParam.objType = this.objType
				window.uploadedImages = ''
			},
		},
		created () {
			this.setImagePickerParam()
			this.setPreviewSize()
		}
	}
</script>
<style lang="less">
	.force-image-picker {
		position: relative;
		width: 100px;
		height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		&:hover {
			border-color: #409EFF;

		}
		.picker-wrapper {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 6px;
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
			background: rgba(0, 0, 0, .3);
			.image-picker-icon {
				font-size: 28px;
				color: #ffffff;
				margin: 5px;
				&:hover {
					color: #409EFF;
				}
			}
			&:hover {

			}
		}
		&.force-image-picker--small {

		}
	}

	.image-picker-icon {
		font-size: 28px;
		color: #8c939d;

	}

	.preview {
		width: 100px;
		height: auto;
		display: block;
	}

	.image-picker-dialog {
		width: 900px;
	}

	#image-selector-current, #image-selector-common {
		width: 100%;
		min-height: 500px;
	}

	#image-selector-common {
		min-height: 600px;
	}

	.size-box {
		position: absolute;
		padding: 0;
		margin: 0;
		left: 100%;
		width: 100%;
		height: 100%;
		line-height: 50px;
		text-align: center;
	}

</style>



