import Vue from 'vue'
import Controller from './controller.vue'

const AppRoutePickerConstructor = Vue.extend(Controller)

let instance
let inserted = false
let installed = false

const initAppRoutePicker = (currentAppRoute) => {
	instance = new AppRoutePickerConstructor({
		el: document.createElement('div'),
		data: {
			route: currentAppRoute,
			// appRoute: urlParse(currentAppRoute, true)
		}
	})
	installed = true
}

const pickAppRoute = (currentAppRoute, callback) => {
	if (!instance) {
		initAppRoutePicker(currentAppRoute)
	}

	if (currentAppRoute) {
		instance.setRoute(currentAppRoute)
	} else {
		instance.setRoute()
	}

	if (typeof(callback) === 'function') {
		instance.callback = callback
	}

	if (!inserted) {
		document.body.appendChild(instance.$el)
		inserted = true
	}
	if (!instance.visible) {
		Vue.nextTick(() => {
			instance.visible = true
		})
	}
}

const clearAppRoute = () => {

	instance.clearAppRoute()
}

const AppRoutePicker = () => {
	if (Vue.prototype.$isServer) return
	return {
		installed,
		pick: pickAppRoute,
		clear: clearAppRoute
	}
}

export default AppRoutePicker

export { AppRoutePicker }



