import imagePicker from './image-picker/index.js'
import ImagePicker from './image-picker/picker.vue'

import pagePreviewer from './page-previewer/index.js'
// import PagePreviewer from './page-previewer/index.vue'
import appRoutePicker from './app-route-picker/index.js'
import AppRoutePicker from './app-route-picker/picker.vue'
export default {
	install (Vue) {
		Vue.component(ImagePicker.name, ImagePicker)
		Vue.prototype.$imagePicker = imagePicker()

		// 路由选择器
		Vue.component(AppRoutePicker.name, AppRoutePicker)
		Vue.prototype.$appRoutePicker = appRoutePicker()

		// 页面预览
		// Vue.component(PagePreviewer.name, PagePreviewer)
		Vue.prototype.$pagePreviewer = pagePreviewer()
	}
}



