<template>
	<div>
		<el-card class="routes-card">
			<el-form label-width="80px">
				<el-form-item v-for="(routeItem, index) in config" :label="routeItem.title+'：'" :key="index">
					<el-button :type="route.selected?'primary':''" v-for="(route, idx) in routeItem.routeList" class="route-button" @click="setCurrentRoute(route)" :key="idx">{{route.name}}</el-button>
				</el-form-item>
			</el-form>
		</el-card>
		<el-card class="routes-params">
			<el-form label-width="110px">
				<el-form-item v-for="(param, index) in routeOptions" :key="index" :label="param.label">
					<el-input @input="setRouteOptions(index, $event)" v-model="param.value" :placeholder="'请输入' + param.label"></el-input>
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>
<script>
	import urlParse from './url-parse'

	export default {
		name: 'route-picker',
		props: {
			appRoute: {
				type: String,
				default: ''
			},
			originConfig: {
				type: Array,
				default: () => ([])
			}
		},
		computed: {
			config () {
				return this.originConfig.map(item => {
					return {
						...item,
						routeList: item.routeList.map(routeItem => {
							return {
								...routeItem,
								selected: false // 附加一个选中开关
							}
						})
					}
				})

			}
		},
		data () {
			return {
				currentRoute: '',
				selectedRoute: '', // 选中的原始路由
				isCustom: false,
				routeOptions: {}
			}
		},
		methods: {
			// 升序函数
			sortAsc (a, b) {
				return a > b
			},
			setCurrentRoute (route) {
				this.isCustom = route.name === '自定义'
				this.selectCurrentRoute(route.route)
				this.selectedRoute = route.route
				this.routeOptions = {}
				if (route.params) {

					Object.keys(route.params).sort(this.sortAsc).map(key => {
						this.$set(this.routeOptions, key, {
							keyword: key,
							label: route.params[key],
							value: ''
						})

					})
				}

				this.emitSelected()
			},
			setRouteOptions (key, value) {
				this.$set(this.routeOptions, key, {
					...this.routeOptions[key],
					value
				})

				this.emitSelected()
			},
			parseRoute () {
				let parsedRoute = this.selectedRoute

				if (JSON.stringify(this.routeOptions) !== '{}') {
					Object.keys(this.routeOptions).sort(this.sortAsc).map(key => {
						const kw = this.routeOptions[key].keyword
						if (kw) {
							parsedRoute = parsedRoute.replace(':' + kw, this.isCustom ? this.routeOptions[key].value : encodeURIComponent(this.routeOptions[key].value))
						}
					})
				}

				return parsedRoute

			},
			selectCurrentRoute (route) {
				console.log(route)
				for (const c in this.config) {
					const cfg = this.config[c]
					for (const r in cfg.routeList) {
						const item = cfg.routeList[r]
						// 1.
						item.selected = false
						if (item.route === route) {
							this.selectedRoute = item.route
							const itemObject = urlParse(item.route)
							const routeObject = urlParse(this.appRoute)
							item.selected = true
							// 设置参数
							this.routeOptions = {}
							if (item.params && itemObject.query) {

								// 筛选出动态参数
								Object.keys(routeObject.query).sort(this.sortAsc).map(key => {

									// 模版占位符
									const param = itemObject.query[key] || ''
									const paramKey = param.replace(/^:/, '')
									if (Object.keys(item.params).indexOf(paramKey) > -1) {
										this.$set(this.routeOptions, key, {
											keyword: paramKey,
											label: item.params[paramKey],
											value: decodeURIComponent(routeObject.query[key] || '')
										})
									}

								})

							}
							if (item.name === '自定义') { //自定义的特殊处理
								console.log('自定义')
								this.routeOptions = {
									url: {
										keyword: 'url',
										label: '完整路由',
										value: this.appRoute
									}
								}
							}
						}

					}
				}
			},
			setSelectedRoute () {
				/* 路由回显有几种情况(为什么写得这么详细，因为被坑了一天一夜)
				* 1. 静态路由：直接比对route
				* 2. 参数路由，分为多种情况，先行判断origin相同的, 可以先比较参数数量，再比较参数名是否全部相同
				* 	2.1 动态参数名相同，但是有不同的静态参数(例如 自动商品详情和普通商品详情)：
				* 	2.2 动态参数名不同，静态参数相同：
				*		2.3 动态参数名和静态参数都不相同：
				*
				* 需要遍历两次，时间复杂度O(n²)
				* */
				const appRoute = this.appRoute

				if (appRoute) {
					const routeObject = urlParse(appRoute)
					// 1. 纯静态先来
					for (const c in this.config) {
						const cfg = this.config[c]
						for (const r in cfg.routeList) {
							const item = cfg.routeList[r]
							// 1.
							if (item.route === appRoute) {
								return this.selectCurrentRoute(item.route)
							}
						}
					}

					// 2. 带参数的

					for (const c in this.config) {
						const cfg = this.config[c]
						for (const r in cfg.routeList) {
							const item = cfg.routeList[r]
							// 1.

							const itemObject = urlParse(item.route)
							// 2.1
							if (routeObject.origin === itemObject.origin) {

								if (item.params) {
									// 先比较所有键值对
									const itemQueryObject = itemObject.query
									const routeQueryObject = routeObject.query
									const itemParamsKeys = Object.keys(item.params)
									const itemQueryKeys = Object.keys(itemQueryObject)
									const routeQueryKeys = Object.keys(routeQueryObject)

									if (itemQueryKeys.length === routeQueryKeys.length) {
										if (itemQueryKeys.sort(this.sortAsc).join(',') === routeQueryKeys.sort(this.sortAsc).join(',')) {
											// 接下来筛选动态参数相同，静态参数值不同的，先拿出动态参数
											const itemDynamicParams = []
											itemQueryKeys.map(key => {
												itemParamsKeys.map(k => {
													if (itemQueryObject[key] === ':' + k) {
														itemDynamicParams.push(key)
													}
												})
											})

											// 去掉动态参数，剩下静态参数
											const itemStaticKeys = itemQueryKeys.filter(key => itemDynamicParams.indexOf(key) === -1)
											let allRight = true
											itemStaticKeys.map(key => {
												itemQueryObject[key] !== routeQueryObject[key] && (allRight = false)
											})
											if (allRight) {
												return this.selectCurrentRoute(item.route)
											}

										}

									}

								}

							}

						}
					}

					// 3. 没有匹配的全都是自定义
					for (const c in this.config) {
						const cfg = this.config[c]
						for (const r in cfg.routeList) {
							const item = cfg.routeList[r]
							// 1.
							if (item.name === '自定义') {
								this.isCustom = true
								return this.selectCurrentRoute(item.route)
							}
						}
					}
				}

			},
			emitSelected () {
				this.$nextTick(() => {
					const parsedRoute = this.parseRoute()
					this.$emit('selected', parsedRoute)
				})

			}
		},
		created () {
			if (this.appRoute) {
				this.setSelectedRoute()
			}
		}
	}
</script>
<style lang="less" scoped>
	.routes-card {
		word-break: break-all;
		background-color: #f5f7f9;
		max-height: 280px;
		overflow-y: auto;
		overflow-x: hidden;
	}

	.route-button {
		border: none;
		margin: .2rem;
	}

	.routes-params {
		background-color: #f5f7f9;
		margin-top: 1rem;
	}

	.fade-enter-active, .fade-leave-active {
		transition: opacity .5s;
	}

	.fade-enter, .fade-leave-to {
		opacity: 0;
	}

</style>



