<template>
	<div class="url-form-container">
		<!--URL复制区-->
		<div class="url-box">
			<h4>页面标题</h4>
			<p class="url-detail">{{title}}</p>
			<div class="copy-button">
				<span class="title-to-copy" :data-clipboard-text="title"><el-button>点击复制标题</el-button></span>
			</div>
			<h4>页面链接</h4>
			<p class="url-detail">{{url}}</p>
			<div class="copy-button">
				<span class="url-to-copy" :data-clipboard-text="url"><el-button>点击复制链接</el-button></span>
				<el-dialog width="30%" title="发送消息" :visible.sync="showSendBox" append-to-body>
					<el-form :model="sendOptions" labelWidth="70px" :rules="sendRules" ref="sendBox">
						<el-form-item label="用户ID" prop="uid">
							<el-input v-model="sendOptions.uid"></el-input>
						</el-form-item>
						<el-form-item label="内容" prop="content">
							<el-input type="textarea" autosize v-model="sendOptions.content"></el-input>
						</el-form-item>
						<el-form-item label="链接">
							<app-route-picker v-model="sendOptions.app_route"></app-route-picker>
						</el-form-item>
						<el-form-item label="账号">
							<el-select v-model="sendOptions.notice_type">
								<el-option value="secretary" label="小秘书"></el-option>
								<el-option value="assistant" label="爱米小助手"></el-option>
							</el-select>
						</el-form-item>
					</el-form>
					<div style="text-align: right; margin: 0">
						<el-button type="text" @click="showSendBox = false">取消</el-button>
						<el-button type="primary" @click="send">发送</el-button>
					</div>
				</el-dialog>
				<el-button @click="showSendBox=true">点击发送消息</el-button>
			</div>
		</div>
		<!--二维码扫描区-->
		<div>
			<div>
				<img :src="dataImage" alt="">
			</div>
			<div class="download-button">
				<a :href="dataImage" :download="pictureName">
					<el-button>点击下载二维码</el-button>
				</a>
			</div>
		</div>
	</div>
</template>
<script>
	import Clipboard from 'clipboard'
	import QRCode from 'qrcode'

	export default {
		name: 'url-form',
		props: {
			url: {
				type: String,
				default: '',
				required: true
			},
			title: {
				type: String,
				default: '',
				required: true
			},
		},
		data () {
			return {
				dataImage: '',
				showSendBox: false,
				sendOptions: {
					uid: '',
					content: this.title,
					notice_type: 'secretary',
					app_route: this.appRoute,
					'app-route-id': '',
					'app-route-params': '',
					uploadedImages: ''
				},
				sendRules: {
					uid: [{
						required: true,
						message: '请输入用户ID',
						trigger: 'blur'
					}],
					content: [{
						pattern: /^(?!.*%)/,
						message: '消息内容不能包含 % ',
						trigger: 'blur'
					}]
				}
			}
		},
		watch: {
			showSendBox () {
				this.sendOptions.content = this.title
				this.sendOptions.app_route = this.appRoute
			},
			url () {
				this.makeLink()
			}
		},
		computed: {
			pictureName () {
				const name = this.url.match(/\?page=(.*)&?/)
				return (name ? name[1] : 'qrcode') + '.jpg'
			},
			appRoute () {
				return 'meijiabang://openurl_in_app?url=' + encodeURIComponent(this.url)
			}
		},
		methods: {
			send () {
				this.$refs.sendBox.validate(async (validation) => {
					if (!validation) return
					const data = new FormData()
					Object.keys(this.sendOptions).map(key => {
						data.append(key, this.sendOptions[key])
					})
					await this.$api.post('/admin.php?m=Admin&c=SystemNotice&a=addSingle', data, {
						headers: {
							'Content-Type': 'multipart/form-data'
						}
					}).then(() => {
						this.$message.success('发送成功！')
					}).catch(() => {
						this.$message.success('发送失败！')
					})
					this.showSendBox = false
				})
			},
			makeLink () {
				QRCode.toDataURL(this.url, (err, data) => {
					if (err) console.log(err)
					this.dataImage = data
				})
			}
		},
		created () {
			this.$nextTick(() => {
				const titleClb = new Clipboard('.title-to-copy')
				const urlClb = new Clipboard('.url-to-copy')
				titleClb.on('success', () => {
					this.$message.success('成功复制页面标题！')
				})
				urlClb.on('success', () => {
					this.$message.success('成功复制页面链接！')
				})

				titleClb.on('error', () => {
					this.$message.error('复制失败！请手动复制')
				})
				urlClb.on('error', () => {
					this.$message.error('复制失败！请手动复制')
				})
				this.makeLink()
			})
		}
	}
</script>
<style lang="less" scoped>
	.url-form-container {
		width: 300px;
		height: @device-height;
		/*border: 1px solid #e9e9e9;*/
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.url-detail {
		margin: 0;
		padding: 10px;
		border: 1px solid #e9e9e9;
		border-radius: 5px;
		word-break: break-all;
	}

	.download-button {
		text-align: center;
	}

	.url-box {
		padding: 10px;
	}

	.copy-button {
		margin-top: 20px;
		text-align: center;
	}

	.url-to-copy {
		margin-right: 20px;
	}
</style>



