<template>
	<el-dialog title="设置UTM追踪统计信息" append-to-body :visible="show" custom-class="utm-content-picker">
		<el-form label-width="100px">
			<el-form-item label="标题">
				<el-input placeholder="输入标题" v-model="utmContent.content_title"></el-input>
			</el-form-item>
			<el-form-item label="业务">
				<el-select class="utm-content-select" placeholder="请选择" v-model="utmContent.content_business" clearable>
					<el-option :value="business" v-for="(business, index) in utmContents.business" :key="index">{{business}}</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="系列活动名">
				<el-select ref="activities" class="utm-content-select" placeholder="请选择" v-model="utmContent.content_act" multiple filterable clearable @keyup.enter.native="selectFirstOne('activities', utmContent.content_act)">
					<el-option :value="activity" v-for="(activity, index) in utmContents.activities" :key="index">{{activity}}</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="标签">
				<el-select ref="tags" class="utm-content-select" placeholder="请选择" v-model="utmContent.content_tags" multiple filterable clearable @keyup.enter.native="selectFirstOne('tags', utmContent.content_tags)">
					<el-option :value="tag" v-for="(tag, index) in utmContents.tags" :key="index">{{tag}}</el-option>
				</el-select>
			</el-form-item>
		</el-form>
		<span slot="footer">
			<el-button @click="cancel">取消</el-button>
			<el-button type="primary" @click="submit">确定</el-button>
		</span>
	</el-dialog>
</template>
<script>
	export default {
		name: 'utm-content-picker',
		props: {
			visible: {
				type: Boolean,
				default: false
			},
			defaultUtmContent: {
				type: Object,
				default: () => ({
					content_title: '',
					content_business: '',
					content_act: [],
					content_tags: []
				})
			},
			utmContents: {
				type: Object,
				default: () => ({})
			}
		},
		computed: {
			list () {
				return this.utmContents
			}
		},
		data () {
			return {
				show: this.visible,
				utmContent: this.defaultUtmContent
			}
		},
		watch: {
			visible (value) {
				this.show = value
			},
			defaultUtmContent (val) {
				this.utmContent = val
			}
		},
		methods: {
			cancel () {
				this.$emit('hide')
			},
			submit () {
				this.$emit('hide')
				console.log(this.utmContent)
				this.$emit('selected', {...this.utmContent})
			},
			selectFirstOne (name, selection) {
				const obj = {
					'activities': this.utmContents.activities || [],
					'tags': this.utmContents.tags || []
				}
				const list = obj[name]
				const $ref = this.$refs[name]
				const query = $ref.query
				const reg = new RegExp(query.replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1'), 'i')
				const first = list.filter(item => reg.test(item) && selection.indexOf(item) === -1)[0]
				if (first) {
					selection.push(first)
					$ref.query = ''
				}

			}
		}
	}
</script>
<style lang="less">
	.utm-content-picker {
		width: 500px;
	}

	.utm-content-select {
		width: 100%;
	}
</style>



