import Vue from 'vue'

const ImagePickerConstructor = Vue.extend()

let instance
let inserted = false
let installed = false

let previewer
let previewerInserted = false
let previewerInstalled = false

const initImagePicker = () => {
	instance = new ImagePickerConstructor({
		el: document.createElement('div'),
		computed: {
			imageTypeLimit () {
				let imageTypeLimit = []
				let imageTypeLimitToBeFilter = []

				if (typeof(this.imageType) === 'string') {
					imageTypeLimitToBeFilter = this.imageType.split(',')
				} else if (Array.isArray(this.imageType)) {
					imageTypeLimitToBeFilter = this.imageType
				} else {
					imageTypeLimitToBeFilter = []
				}

				for (let key in imageTypeLimitToBeFilter) {
					if (this.imageTypeSupport.indexOf(imageTypeLimitToBeFilter[key]) >= 0) {
						imageTypeLimit.push(imageTypeLimitToBeFilter[key])
					}
				}

				return imageTypeLimit.join(',')
			},
		},
		data: () => ({
			imageTypeSupport: [
				'jpg',
				'jpeg',
				'gif',
				'bmp',
				'png',
				'webp',
			],
			objId: 0,
			objType: '',
			imageType: [],
			visible: true,
			activeTab: 'current'
		}),
		render (h) {
			// --- 模态框 start ---
			return h('el-dialog', {
				props: {
					visible: this.visible,
					'lock-scroll': true,
					'append-to-body': true,
					'close-on-click-modal': false,
					title: '图片选择器',
					width: '900px'

				},
				'class': [],
				on: {
					close: () => {
						this.visible = false
					}
				}
				// --- 模态框 end ---
			}, [
				// --- 标签 start ---
				h('el-tabs', {
					props: {
						value: this.activeTab,
						type: 'card'
					},
					on: {
						['tab-click']: (tab) => {
							this.activeTab = tab.name
						}
					}
				}, [
					h('el-tab-pane', {
						props: {
							label: '本页',
							name: 'current'
						}
					}, [
						h('iframe', {
							attrs: {
								src: `/admin.php?c=Image&a=current&objId=${this.objId}&objType=${this.objType}&imageType=${this.imageTypeLimit}`,
								frameborder: 0
							},
							style: {
								width: '100%',
								'min-height': '500px'
							}
						})
					]),
					h('el-tab-pane', {
						props: {
							label: '公共  ',
							name: 'common'
						}
					}, [
						h('iframe', {
							attrs: {
								src: `/admin.php?c=Image&a=index&imageType=${this.imageTypeLimit}`,
								frameborder: 0
							},
							style: {
								width: '900px',
								'min-height': '500px'
							}
						})
					])
				])])
		}
	})
	installed = true
}

const initImagePreviewer = (src) => {
	previewer = new ImagePickerConstructor({
		el: document.createElement('div'),
		data: () => ({
			src,
			visible: true
		}),
		render (h) {
			// --- 模态框 start ---
			return h('el-dialog', {
				props: {
					visible: this.visible,
					'lock-scroll': true,
					'append-to-body': true,
					'close-on-click-modal': false,
					title: '图片预览',
					width: '600px'

				},
				'class': [],
				on: {
					close: () => {
						this.visible = false
					}
				}
				// --- 模态框 end ---
			}, [
				h('div', {
					style: {
						'text-align': 'center'
					}
				}, [
					h('img', {
						domProps: {
							src: this.src
						},
						style: {
							width: '100%'
						}
					})
				]),

			])
		}
	})
	previewerInstalled = true
}

const pickImage = () => new Promise((resovle) => {
	if (!instance) {
		initImagePicker()

	}

	if (!inserted) {
		document.body.appendChild(instance.$el)
		inserted = true
	}
	if (!instance.visible) {
		Vue.nextTick(() => {
			instance.visible = true
		})
	}
})

const hidePicker = () => {
	if (!instance) {
		initImagePicker()
		return
	}

	if (!inserted) {
		return
	}
	if (!instance.visible) {
		return
	}

	Vue.nextTick(() => {
		instance.visible = false
	})
}

const previewImage = (src) => {
	if (!src) return
	if (!previewer) {
		initImagePreviewer(src)
	}

	previewer.src = src

	if (!previewerInserted) {
		document.body.appendChild(previewer.$el)
		previewerInserted = true
	}
	if (!previewer.visible) {
		Vue.nextTick(() => {
			previewer.visible = true
		})
	}
}

const ImagePicker = () => {
	if (Vue.prototype.$isServer) return
	return {
		pick: pickImage,
		hide: hidePicker,
		preview: previewImage
	}
}

export default ImagePicker

export { ImagePicker }



