<template>
	<el-dialog :visible="visible" lock-scroll append-to-body :close-on-click-modal="false" title="路由选择器" width="900px" @close="close">
		<route-picker v-if="config.length&&visible" @selected="selectAppRoute" :app-route="pureRoute" ref="routes" :origin-config="config"></route-picker>
		<utm-content-picker @show="showUtmContent=true" @hide="showUtmContent=false" v-if="needUtmContent" :visible="showUtmContent&&visible" @selected="selectUtmContent" :default-utm-content="utmContent" :utm-contents="utmContents"></utm-content-picker>
		<span slot="footer">
			<el-button @click="close">取消</el-button>
			<el-button type="primary" @click="submit">确定</el-button>
		</span>
	</el-dialog>
</template>
<script>
	import RoutePicker from './route-picker.vue'
	import UtmContentPicker from './utm-content-picker.vue'

	export default {
		name: 'route-controller',
		components: {
			RoutePicker,
			UtmContentPicker
		},
		props: {
			needUtmContent: {
				type: Boolean,
				default: true
			}
		},
		data () {
			return {
				route: '',
				pureRoute: '',
				visible: false,
				showUtmContent: false,
				config: [],
				utmContentString: '',
				utmContents: {}
			}
		},
		computed: {
			utmContent () {
				return this.dropUtmContents(this.route).utmContent
			}
		},

		methods: {
			callback (route) {
				console.log(111, route)
			},
			close () {
				this.visible = false
			},
			submit () {
				if (this.currentRoute) {
					if (this.needUtmContent === false) {
						this.visible = false
					} else {
						this.showUtmContent = true
					}
				} else {
					this.$message.warning('请先选择路由！')
				}
			},
			getRouteConfig () {
				this.$api.get('/boss/backend/admin/app-route/find-list').then(res => {
					const routes = res.data
					this.config = routes.config
					this.utmContents = routes.utm_content
				})
			},
			setRoute (route) {
				this.route = route
				this.pureRoute = this.dropUtmContents(this.route).route
			},
			selectAppRoute (route) {
				console.log(route)
				this.currentRoute = this.dropUtmContents(route).route
			},
			selectUtmContent (utmContent = {}) {
				// 编码
				const encodedContent = []
				Object.keys(utmContent).map(key => {
					if (typeof(utmContent[key]) === 'string') {
						encodedContent.push(`${key}=${encodeURIComponent(utmContent[key])}`)
					} else if (Array.isArray(utmContent[key])) {
						encodedContent.push(`${key}=${encodeURIComponent(utmContent[key].join(','))}`)
					}

				})

				this.showUtmContent = false
				this.visible = false
				this.utmContentString = encodedContent.join('&')
				this.callback(this.combinedRoute())

			},
			combinedRoute () {
				if (this.currentRoute.indexOf('?') > -1) {
					return this.currentRoute + '&' + this.utmContentString
				} else {
					return this.currentRoute + '?' + this.utmContentString
				}
			},
			dropUtmContents (route = '') {
				const content = {
					content_title: '',
					content_business: '',
					content_act: [],
					content_tags: []
				}
				const utmKeys = Object.keys(content)

				let routeOrigin
				let routeParams

				const temp = []

				routeOrigin = route.split('?')[0]
				routeParams = route.split('?')[1]

				let thinRoute = routeOrigin

				if (routeParams) {
					routeParams.split('&').map(param => {

						const key = param.split('=')[0]
						// 过滤掉utm
						const value = param.split('=')[1] || ''

						if (utmKeys.indexOf(key) === -1) {
							temp.push(key + '=' + value)
						} else {
							if (['content_act', 'content_tags'].indexOf(key) > -1) {
								const value = param.split('=')[1]
								if (value) {
									content[key] = decodeURIComponent(value).split(',')
								}

							} else {
								content[key] = decodeURIComponent(param.split('=')[1] || '') || ''
							}
						}

					})
				}
				if (temp.length) {
					thinRoute = routeOrigin + '?' + temp.join('&')
				}
				return {
					route: thinRoute,
					utmContent: content
				}
			}
		},
		created () {
			this.currentRoute = this.route
			this.getRouteConfig()
		}
	}
</script>
<style>

</style>



